<template>
  <v-layout row justify-center>
    <v-dialog v-model="showDialog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">Headline</v-card-title>
        <v-card-text>todo add key info</v-card-text>
        <v-card-text>Remember to sign out or you key info is not secured.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="closeDialog()">OK</v-btn>
          <v-btn color="blue darken-1" flat @click="signOut()">Sign Out</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import { EventBus } from '@/eventBus'

export default {
  name: 'ModalDialog',
  data () {
    return {
      showDialog: false
    }
  },
  mounted () {
    let vm = this
    EventBus.$on('ModalDialog', function (val) {
      vm.showDialog = val.showDialog
    })
  },
  methods: {
    async closeDialog(showDialog) {
      console.log('closeDialog showDialog')

      this.showDialog = showDialog
      await this.$emit('closeModal')
    },
    async signOut(showDialog) {
      console.log('signOut showDialog')

      this.showDialog = showDialog
      await this.$emit('closeModalSignOut')
    }
  }
}
</script>